import "./App.css";
import { Form, Button, Checkbox, DatePicker, Input, Select, Space } from "antd";

function AppF() {
  return (
    <div className="App">
      <header className="App-header">
        <Form
          autoComplete="off"
          labelCol={{ span: 10 }}
          wrapperCol={{ span: 14 }}
          onFinish={(values) => {
            console.log({ values });
          }}
          onFinishFailed={(error) => {
            console.log({ error });
          }}
        >
          <Form.Item
            name="fullName"
            label="名前"
            rules={[
              {
                required: true,
                message: "名前書いてください。",
              },
              { whitespace: true },
              { min: 3 },
            ]}
            hasFeedback
          >
            <Input placeholder="名前を入力" />
          </Form.Item>

          <Form.Item
            name="email"
            label="メールアドレス"
            rules={[
              {
                required: true,
                message: "メールアドレスを書いてください。",
              },
              { type: "email", message: "正しいメールを入れてください。" },
            ]}
            hasFeedback
          >
            <Input placeholder="メールアドレスを書いてください。" />
          </Form.Item>

          <Form.Item
            name="password"
            label="暗証番号"
            rules={[
              {
                required: true,
              },
              { min: 6 },
              {
                validator: (_, value) =>
                  value && value.includes("A")
                    ? Promise.resolve()
                    : Promise.reject("暗証番号を基準に合ってないです。例えば123ASD"),
              },
            ]}
            hasFeedback
          >
            <Input.Password placeholder="暗証番号を入れてください。" />
          </Form.Item>

          <Form.Item
            name="confirmPassword"
            label="暗証番号確認"
            dependencies={["password"]}
            rules={[
              {
                required: true,
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || getFieldValue("password") === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(
                    "暗証番号同じくないです。"
                  );
                },
              }),
            ]}
            hasFeedback
          >
            <Input.Password placeholder="暗証番号確認" />
          </Form.Item>

          <Form.Item name="gender" label="性別" >
            <Select placeholder="性別">
              <Select.Option value="male">男性</Select.Option>
              <Select.Option value="female">女性</Select.Option>
            </Select>
          </Form.Item>

          <Form.Item
            name="dob"
            label="誕生日"
            rules={[
              {
                required: true,
                message: "誕生日を入力してください。",
              },
            ]}
            hasFeedback
          >
            <DatePicker
              style={{ width: "100%" }}
              picker="date"
              placeholder="日付選択"
            />
          </Form.Item>

          <Form.Item
            name="website"
            label="ホームページ"
            rules={[{ type: "url", message: "正しいホームページを入力してください。" }]}
            hasFeedback
          >
            <Input placeholder="ホームページを入力" />
          </Form.Item>

          <Form.Item
            name="agreement"
            wrapperCol={{ span: 24 }}
            valuePropName="checked"
            rules={[
              {
                validator: (_, value) =>
                  value
                    ? Promise.resolve()
                    : Promise.reject(
                        "続行するには、利用規約に同意する必要があります"
                      ),
              },
            ]}
          >
            <Checkbox>
              {" "}
              条件に同意する <a href="#">内容</a>
            </Checkbox>
          </Form.Item>

          <Form.Item wrapperCol={{ span: 24 }}>
            <Button block type="primary" htmlType="submit">
              登録
            </Button>
          </Form.Item>
        </Form>
      </header>
    </div>
  );
}

export default AppF;